<template>
  <div id="app">
    <div>{{msg}}</div>
    <div>{{number}}</div>
    <div id="myChart" :style="{width: '300px', height: '300px'}"></div>
  </div>
</template>

<script>
import Vue from 'vue'
import echarts from 'echarts'
Vue.prototype.$echarts = echarts

export default {
  name: 'ding1',
  data () {
    return {
      msg: '这里是ding1'
    }
  },
  props: ['number', 'text', 'dataName', 'dataNumber'],
  mounted(){
    this.drawLine();
  },
  methods: {
    drawLine(){
      // 基于准备好的dom，初始化echarts实例
      const myChart = this.$echarts.init(document.getElementById('myChart'));
      // 绘制图表
      myChart.setOption({
        title: { text: this.text || '在Vue中使用echarts' },
        tooltip: {},
        xAxis: {
          data: this.dataName || ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'bar',
          data: this.dataNumber || [5, 20, 36, 10, 10, 20]
        }]
      });
    }
  }
}

</script>

<style scoped>
#app {
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
